/*type color variables */
/*border color variables */
/*type size variables */
#map.graphs .leaflet-control-zoom {
  display: none;
}
.tabs {
  z-index: 1;
  position: relative;
  float: right;
  line-height: 30px;
  margin-right: 16px;
  margin-top: 16px;
  text-align: right;
  border-bottom: 1px solid #e7e7e7;
}
.tabs > div {
    display: inline-block;
    text-align: center;
    background-color: #f6f6f6;
    width: 95px;
}
.tabs > div.selected {
      background-color: white;
}
.tabs > div.disabled {
      opacity: 0.3;
      pointer-events: none;
}
.tabs > div:first-of-type {
      border-right: 1px solid #E7E7E7;
      margin-right: -4px;
}
@media only screen and (min-width: 0) and (max-width: 950px) {
.tabs {
    float: none;
    width: 100%;
}
.tabs > div {
      width: 50%;
}
}



