
#cityContainer.urban-extent #map-key .map-key-years .map-key-year:first-of-type .map-key-checkbox {
  border: 1px solid #2E2E76;
}
#cityContainer.urban-extent #map-key .map-key-years .map-key-year:first-of-type.always .map-key-checkbox, #cityContainer.urban-extent #map-key .map-key-years .map-key-year:first-of-type.current .map-key-checkbox {
  background-color: #2E2E76;
}
#cityContainer.urban-extent #map-key .map-key-years .map-key-year:nth-of-type(2) .map-key-checkbox {
  border: 1px solid #AC2341;
}
#cityContainer.urban-extent #map-key .map-key-years .map-key-year:nth-of-type(2).always .map-key-checkbox, #cityContainer.urban-extent #map-key .map-key-years .map-key-year:nth-of-type(2).current .map-key-checkbox {
  background-color: #AC2341;
}
#cityContainer.urban-extent #map-key .map-key-years .map-key-year:nth-of-type(3) .map-key-checkbox {
  border: 1px solid #DA8B40;
}
#cityContainer.urban-extent #map-key .map-key-years .map-key-year:nth-of-type(3).always .map-key-checkbox, #cityContainer.urban-extent #map-key .map-key-years .map-key-year:nth-of-type(3).current .map-key-checkbox {
  background-color: #DA8B40;
