#map-key {
  position: fixed;
  top: 101px;
  right: 16px;
  width: 190px;
  background-color: white;
  color: #5A5A5A;
  font-size: 12px;
  line-height: 16px;
  z-index: 2;
}
#map-key .satellite {
    border-top: 1px solid #e7e7e7;
}
#map-key .satellite > div {
      padding: 14px;
      width: 50%;
      display: inline-block;
      text-align: center;
      cursor: pointer;
}
#map-key .satellite > div.selected, #map-key .satellite > div:hover {
        background-color: #f6f6f6;
}
#map-key .map-key-years {
    padding: 14px;
    border-bottom: 1px solid #e7e7e7;
}
#map-key .map-key-years .map-key-year {
      margin: 14px 0px;
      cursor: pointer;
}
#map-key .map-key-years .map-key-year:first-of-type .map-key-checkbox {
        border: 1px solid #ac2341;
}
#map-key .map-key-years .map-key-year:first-of-type.always .map-key-checkbox, #map-key .map-key-years .map-key-year:first-of-type.current .map-key-checkbox {
        background-color: #ac2341;
}
#map-key .map-key-years .map-key-year:nth-of-type(2) .map-key-checkbox {
        border: 1px solid #da8b40;
}
#map-key .map-key-years .map-key-year:nth-of-type(2).always .map-key-checkbox, #map-key .map-key-years .map-key-year:nth-of-type(2).current .map-key-checkbox {
        background-color: #da8b40;
}
#map-key .map-key-years .map-key-year.always {
        cursor: normal;
        pointer-events: none;
}
#map-key .map-key-years .map-key-year.always .map-key-checkbox, #map-key .map-key-years .map-key-year.current .map-key-checkbox {
        position: relative;
}
#map-key .map-key-years .map-key-year.always .map-key-checkbox:after, #map-key .map-key-years .map-key-year.current .map-key-checkbox:after {
          content: '';
          background-image: url("/img/check.svg");
          position: absolute;
          left: 0px;
          top: 0px;
          width: 100%;
          height: 100%;
          background-size: 12px 10px;
          background-position: center center;
          display: block;
          background-repeat: no-repeat;
}
#map-key .map-key-years .map-key-year .map-key-period {
        display: inline-block;
}
#map-key .map-key-years .map-key-year .map-key-checkbox {
        margin-right: 8px;
        display: inline-block;
        width: 16px;
        height: 16px;
        background: #FFFFFF;
        box-shadow: inset 0 1px 3px 0 rgba(0, 0, 0, 0.5);
        border-radius: 2px;
}
#map-key .showLayers {
    padding: 16px;
    font-weight: bold;
}
#map-key .map-key-layers {
    padding: 16px;
}
#map-key .map-key-layers .layer-action {
      margin: 16px 0px;
}
#map-key .map-key-layers .layer-action.no-click {
        pointer-events: none;
        cursor: default;
}
#map-key .map-key-layers .layer-action:first-of-type {
        margin-top: 0px;
}
#map-key .map-key-layers .layer-action:last-of-type {
        margin-bottom: 0px;
}
#map-key .map-key-layers .layer-action .layer-checkbox {
        width: 16px;
        height: 16px;
        border-radius: 999px;
        display: block;
        float: left;
        border: 1px solid #E7E7E7;
        background-color: white;
}
#map-key .map-key-layers .layer-action .layer-title {
        margin-left: 32px;
        line-height: 16px;
}

/*type color variables */
/*border color variables */
/*type size variables */
@media only screen and (min-width: 0) and (max-width: 950px) {
#map-key {
    position: static;
    width: 100%;
}
}



