/*type color variables */
/*border color variables */
/*type size variables */
.change-years {
  padding-bottom: 16px;
  padding-top: 16px;
}
.change-year {
  cursor: pointer;
  display: inline-block;
  float: left;
  width: 100px;
  border: 1px solid #e7e7e7;
  text-align: center;
  font-size: 12.5px;
  line-height: 29px;
  height: 29px;
  color: #929292;
}
.change-year.currentYear {
    font-weight: bold;
    color: #5A5A5A;
    background-color: #f6f6f6;
}
#section-graphs {
  position: relative;
  z-index: 1;
  padding-left: 306px;
  padding-right: 16px;
}
#section-graphs > div {
    background-color: white;
}
canvas {
  max-width: 100%;
  margin-left: auto;
}
@media only screen and (min-width: 0) and (max-width: 950px) {
#section-graphs {
    padding-left: 0px;
    padding-right: 0px;
}
#section-graphs > div {
      background-color: white;
}
canvas {
    margin: auto;
    margin-top: 32px;
}
.hold-legend {
    text-align: center;
    margin-bottom: 32px;
}
.legend-ul {
    display: inline-block;
    margin-left: auto;
    margin-right: auto;
}
}



