
#section-summary {
  background-color: white;
  font-size: 11px;
  line-height: 11px;
  padding: 12px;
}
#section-summary .grid {
    padding-bottom: 0px;
    margin-bottom: 10px;
}
#section-summary .more-title {
    margin-top: 0px;
}
#section-summary .col-1-2:first-of-type > div,
  #section-summary .col-1-3:first-of-type > div {
    padding-left: 0px;
}
#section-summary .col-1-2:first-of-type .summary-sub-title-num,
  #section-summary .col-1-3:first-of-type .summary-sub-title-num {
    border-left: none;
}
#section-summary .col-1-2 > div,
  #section-summary .col-1-3 > div {
    padding: 0px 6px;
}
#section-summary .summary-sub-title-num {
    color: #5A5A5A;
    border-bottom: 1px solid #e7e7e7;
    border-left: 1px solid #e7e7e7;
    line-height: 17px;
}
#section-summary .summary-sub-title {
    color: #929292;
}
#section-summary .value {
    color: #222222;
    margin-bottom: 12px;
}



