
#popup[data-v-e6a78fd6] {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 3;
}
#popup #popup-text[data-v-e6a78fd6] {
    width: 480px;
    max-width: calc(100% - 20px);
