/*type color variables */
/*border color variables */
/*type size variables */
.cursor {
  cursor: pointer;
}
.titleCase {
  text-transform: capitalize;
}
#cityContainer {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-color: #C5C5C5;
  background-blend-mode: multiply;
  /* no ie support */
  /* Rectangle 16: */
  min-height: 100vh;
  padding-top: 54px;
}
#cityContainer #city-header-data {
    color: white;
    text-align: center;
    margin-top: calc(50vh - 112px);
}
#cityContainer #city-header-data .title {
      font-size: 14px;
      line-height: 17px;
}
#cityContainer #city-header-data .value {
      font-size: 60px;
      line-height: 72px;
}
#cityContainer #city-header-data .unit {
      font-size: 12px;
      line-height: 14px;
}
#cityContainer.city-header {
    padding-left: 0px;
}
#cityContainer .header-title {
    background-color: black;
    font-size: 20px;
    line-height: 43px;
    position: relative;
    z-index: 2;
    padding-left: 20px;
}
#side-panel {
  z-index: 2;
  position: absolute;
  left: 0px;
  top: 54px;
  color: #5A5A5A;
  padding: 14px;
}
.summary,
.dropdown-nav {
  width: 274px;
}
.dropdown-nav {
  color: #5A5A5A;
  font-size: 13px;
  line-height: 28px;
  position: relative;
  background-color: white;
}
.dropdown-nav.open .dropdown-arrow img {
    -webkit-transform: rotate(180DEG);
            transform: rotate(180DEG);
}
.dropdown-nav .dropdown-title {
    cursor: pointer;
    font-weight: bold;
}
.dropdown-nav .currentSection {
    padding: 0 12px;
    border-bottom: 1px solid grey;
}
.dropdown-nav .currentSection > div {
      display: inline-block;
}
.dropdown-nav .currentSection .dropdown-arrow {
      float: right;
}
.dropdown-nav .currentSection .dropdown-arrow img {
        width: 15px;
        vertical-align: middle;
        cursor: pointer;
        transition: -webkit-transform 500ms ease;
        transition: transform 500ms ease;
        transition: transform 500ms ease, -webkit-transform 500ms ease;
}
.dropdown-nav .allSections {
    z-index: 4;
    background-color: white;
    width: 100%;
    top: 30px;
    left: 0px;
    position: absolute;
    border-top: 1px solid #E8E8E8;
}
.dropdown-nav .allSections > div {
      padding: 0 15px;
}
.city-header #side-panel {
  background-color: transparent;
  box-shadow: none;
  color: white;
}
.city-header #side-panel .top-box {
    background-color: transparent;
}
.open .more-title:after {
  background-image: url("/img/minus.svg");
}
.more-title {
  margin-top: 17px;
  font-size: 10px;
  line-height: 12px;
  text-transform: uppercase;
  color: #929292;
  position: relative;
}
.more-title:after {
    display: block;
    content: '';
    position: absolute;
    right: 0px;
    font-size: 14px;
    bottom: 0;
    height: 100%;
    width: 8px;
    background-image: url("/img/plus.svg");
    background-position: center center;
    background-size: 8px;
    background-repeat: no-repeat;
}
.show-more > div:first-of-type {
  margin-top: 20px;
}
.show-more .label, .show-more .amount {
  line-height: 18.5px;
}
.show-more .amount {
  padding-bottom: 1em;
}
.show-more .amount:last-of-type {
    padding-bottom: 0px;
}
@media only screen and (min-width: 0) and (max-width: 950px) {
#side-panel {
    position: static;
    width: 100%;
}
#side-panel .allSections {
      top: 0px;
      position: static;
}
#side-panel .summary,
    #side-panel .dropdown-nav {
      width: 100%;
}
#cityContainer #city-header-data {
    margin-top: 0px;
}
#cityContainer #city-header-data > div {
      margin: 20px 0px;
}
#cityContainer #city-header-data .value {
      font-size: 48px;
      line-height: 48px;
}
}



